.leftContainer,
.rightContainer {
  height: 100vh;
  float: left;
  overflow: scroll;
}

.leftContainer {
  width: 70%;
}

.text {
  width: 50%;
  margin: 20px auto;
}

.rightContainer {
  width: 30%;
}

.createHighlightContainer {
  background-color: #e8e8e8;
}

.fixedContainer {
  position: fixed;
  width: 100%;
  height: 100%;
}

.addFormContainer {
  width: 50%;
  height: 60%;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #e8e8e8;
  border: 2px solid black;
  border-radius: 2px;
}

.addForm {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 70%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.phraseInfo {
  overflow-x: scroll;
}

